body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  padding: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  font-size: 14px;
}

.lds-facebook {
  -webkit-align-content: center;
          align-content: center;
  display: inline-block;
  position: relative;
  width: 300px;
  height: 300px;
  margin-left: 49%;
  margin-top: 40%;
}
.lds-facebook div {
  display: inline-block;
  position: absolute;
  left: 12px;
  width: 26px;
  background: #3C5A99;
  -webkit-animation: lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
          animation: lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
}
.lds-facebook div:nth-child(1) {
  left: 12px;
  -webkit-animation-delay: -0.24s;
          animation-delay: -0.24s;
}
.lds-facebook div:nth-child(2) {
  left: 52px;
  -webkit-animation-delay: -0.12s;
          animation-delay: -0.12s;
}
.lds-facebook div:nth-child(3) {
  left: 90px;
  -webkit-animation-delay: 0;
          animation-delay: 0;
}
@-webkit-keyframes lds-facebook {
  0% {
    top: 12px;
    height: 102px;
  }
  50%, 100% {
    top: 38px;
    height: 52px;
  }
}
@keyframes lds-facebook {
  0% {
    top: 12px;
    height: 102px;
  }
  50%, 100% {
    top: 38px;
    height: 52px;
  }
}

.Movie {
  background-color: white;
  width: 40%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin-bottom: 50px;
  text-overflow: ellipsis;
  padding: 0 25px;
  box-shadow: 0 8px 38px rgba(133, 133, 133, 0.3), 0 5px 12px rgba(133, 133, 133, 0.22);
}

.Movie__Column {
  width: 30%;
  box-sizing: border-box;
  text-overflow: ellipsis;
}

.Movie__Column:last-child {
  padding: 20px 0;
  width: 60%;
}

.Movie h1 {
  font-size: 20px;
  font-weight: 600;
}

.Movie .Movie__Genre {
  display: inline;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.Movie__Genres .Movie__Genre {
  margin-right: 5px;
  color: #565656;
  list-style: circle;
}

.Movie .Movie__Synopsis {
  text-overflow: ellipsis;
  color: #232323;
  margin-top: 10px;
}

.Movie .Movie__Poster {
  max-width: 100%;
  position: relative;
  top: -10px;
  box-shadow: -10px 19px 38px rgba(83,83, 83, 0.3), 10px 15px 12px rgba(80, 80, 80. 0.22);
}

h3 {
  margin-top: 0px;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
}

h5 {
  margin: 0;
}
table {
  margin: 0 auto;
  text-align: center;
  border-collapse: collapse;
  border: 1px solid #d4d4d4;
  font-size: 20px;
  background: #fff;
}

table th,
table tr:nth-child(2n+2) {
  background: #e7e7e7;
}

table th,
table td {
  padding: 20px 50px;
}

table th {
  border-bottom: 1px solid #d4d4d4;
}

.stars-outer {
  display: inline-block;
  position: relative;
  font-family: FontAwesome;
}

.stars-outer::before {
  content: "\F006   \F006   \F006   \F006   \F006";
}

.stars-inner {
  position: absolute;
  top: 0;
  left: 0;
  white-space: nowrap;
  overflow: hidden;
  width: 0;
}

.stars-inner::before {
  content: "\F005   \F005   \F005   \F005   \F005";
  color: #f8ce0b;
}

@media screen and (min-width: 320px) and (max-width:667px) {
  .Movie{
    width: 100%;
  }
}

@media screen and (min-width: 320px) and (max-width: 667px) and (orientation: portrait) {
  .Movie{
    width: 100%;
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .Movie__Poster {
    top: 0;
    left: 0;
    width: 100%;
  }
  .Movie__Column {
    width: 100% !important;
  }
}

